<template>
	<div id="coupon">
		<div class="white_box"></div>
		<header class="clearfix">
			<div class="back" @click="$router.go(-1)"></div>
			<div class="title">领券中心</div>
			<div class="cart"  @click="goToCart()"></div>
		</header>
		<section>
			<div class="myCoupon">我的优惠券</div>
			<div v-for="item in couponList" class="cou_box box">
				<img :src="item.img" alt="">
				<div class="cou_info box2">
					<div class="type">{{item.type}}</div>
					<div class="box coupon_box">
						<div class="coupon">{{item.coupon}}</div>
						<div class="condition">{{item.condition}}</div>
					</div>
				</div>
				<div class="get" @click="get(item.id)">领取</div>
			</div>		
		</section>
	</div>
</template>
<script>
	import { Toast } from 'mint-ui';
	export default{
		name:"coupon",
		data(){
			return{
				couponList:[],
				geted:false
			}
		},
		methods:{
			getCouponList(){
				this.$http.get('./static/allList.json')
				.then(response=>{
					this.couponList = response.data.couponList
					console.log(this.couponList)
				})
			},
			get(info){
				console.log(info)
				if (this.geted == false) {
					Toast('您已成功领取');
					this.geted = true;
				}else{
					Toast('您已领取过优惠券');
				}
				
			}
		},
		created(){
			this.getCouponList()
		}
	}
</script>
<style scoped>
	.white_box{
		height: 10px;
		position: fixed;
		width: 100%;
		top: 0;
		background-color: white;
		z-index: 100;
	}
	header{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		height: 50px;
		background-color: white;
		position: fixed;
		top: 10px;
		width: 98%;
		z-index: 100;
	}
	header>div{
		margin-top: 5px;
	}
	header .back{
		width: 40px;
		height: 40px;
		background-size: 40%;
		background-repeat: no-repeat;
		margin-top: 5px;
		background-image: url(../assets/nav_ht@2x.png);
	}
	header .title{
		font-size: 18px;
		height: 40px;
		color: #515151;
	}
	header .cart{
		width: 40px;
		height: 40px;
	}
	.box,.box3{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		margin-top: 20px;
	}
	.box{
		height: 150px;
	}
	.box2{
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
	section{
		margin-top: 70px;
	}
	.myCoupon{
		width: 90%;
		margin: auto;
		height: 35px;
		line-height: 35px;
		text-align: center;
		background-color: #a82d2e;
		font-size: 18px;
		color: white;
	}
	.cou_box{
		height: 80px;
		width: 100%;
	}
	.cou_box>img{
		height: 80px;
		width: 80px;
	}
	.cou_info{
		width: 40%;
	}
	.type{
		line-height: 30px;
	}
	.coupon_box{
		color: #a82d2e;
	}
	.coupon{
		font-size: 18px;
		width: 15%;
	}
	.condition{
		font-size: 14px;
		line-height: 30px;
		margin-left: 10px;
		width: 70%;
		text-align: left;
	}
	.get{
		width: 15%;
		height: 80px;
		line-height: 80px;
		text-align: center;
		background-color: #a82d2e;
		color: white;
	}
</style>


