<template>
	<div id="rank">
		<div class="white_box"></div>
		<header>
			<div class="back" @click="$router.go(-1)"></div>
			<div class="title">排行榜</div>
			<div class="cart"></div>
		</header>
		<section>
			<div class="box list_box">
				<div @click = "toFirst()" :class="{active:selected == '1'}">高人气菜谱</div>
				<div @click = "toSecond()" :class="{active:selected == '2'}">作品达人</div>
			</div>
			<div class="info_box">
				<div v-if="selected==1">
					<ul>
						<li v-for="item in menuList" @click="getmenuinfo(item)">
							<div class="img_box">
								<img :src="item.img" alt="">
							</div>
							<div class="info_box clearfix">
								<div class="fl">
									<span>{{item.name}}</span>
									<div>
										<span class="score">{{item.score}}分</span>
										<span class="peopledid">{{item.peopledid}}人做过</span>
									</div>
								</div>
								<div class="fr">
									<img :src="item.headimg" alt=""><br>
									<div class="username">{{item.username}}</div>
								</div>
							</div>
						</li>
					</ul>
				</div>
				<div v-if="selected==2">
					<div v-for="item in perInfo" class="info_box2 box">
						<div class="rank">第{{item.rank}}名</div>
						<div>
							<img :src="item.head" alt="">
						</div>
						<div class="id">{{item.id}}</div>
						<div class="count">作品人气 {{item.count}}</div>
					</div>
				</div>
			</div>
		</section>
	</div>
</template>
<script>
	export default{
		name:"rank",
		data(){
			return{
				selected:'1',
				menuList:[],
				perInfo:[]
			}
		},
		methods:{
			toFirst(){
				this.selected = 1
			},
			toSecond(){
				this.selected = 2
			},
			getMenuList(){
				this.$http.get('./static/menuList.json')
				.then(response=>{
					this.menuList = response.data.menuList;
					// console.log(this.menuList);
				})
			},
			getPerInfo(){
				this.$http.get('./static/menuList.json')
				.then(response=>{
					this.perInfo = response.data.perInfo;
				})
			},
			getmenuinfo(info){
				this.$store.dispatch('setDetail1',info);
				this.$router.push({path:'/Detail'})
			}
		},
		created(){
			this.getMenuList(),
			this.getPerInfo()
		}
	}
</script>
<style scoped>
	.white_box{
		height: 10px;
		position: fixed;
		width: 100%;
		top: 0;
		background-color: white;
		z-index: 100;
	}
	header{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		height: 50px;
		background-color: white;
		position: fixed;
		top: 10px;
		width: 98%;
		z-index: 100;
	}
	header>div{
		margin-top: 5px;
	}
	header .back{
		width: 40px;
		height: 40px;
		background-size: 40%;
		background-repeat: no-repeat;
		margin-top: 5px;
		background-image: url(../assets/nav_ht@2x.png);
	}
	header .title{
		font-size: 18px;
		height: 40px;
		color: #515151;
	}
	header .cart{
		width: 40px;
		height: 40px;
		color: red;
	}
	.box{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
	}
	section{
		margin-top: 70px;
	}
	.list_box>div{
		width: 48%;
		line-height: 35px;
		font-size: 14px;
		text-align: center;
		position: relative;
	}
	.active{
		color: red;
	}
	.active:after{
		width: 80%;
		height: 2px;
		position: absolute;
		left: 10%;
		bottom: 0;
		content: '';
		background-color: red;
	}
	section ul{
		padding: 0;
	}
	section .img_box{
		width: 100%;
		margin-top: 25px;
	}
	section .img_box img{
		width: 100%;
	}
	section .info_box{
		margin-top: 10px;
	}
	section .info_box .fl>span{
		font-size: 24px;
		line-height: 70px;
		font-weight: bold;
		color: #515151;
	}
	section .info_box .fl div{
		font-size: 12px;
		color: #999999;
	}
	section .info_box .fl div .peopledid{
		margin-left: 15px;
	}
	section .fr img{
		width: 60px;
		height: 60px;
		border-radius: 50%;
	}
	section .fr div{
		font-size: 12px;
		color: #999999;
		text-align: center;
	}
	.info_box2{
		width: 100%;
		height: 50px;
		line-height: 50px;
		margin-top: 20px;
	}
	.info_box2>div{
		width: 24%;
	}
	.info_box2>.rank{
		font-size: 15px;
		color: #333333;
	}
	.info_box2 img{
		margin-top: 5px;
		widows: 40px;
		height: 40px;
		border-radius: 50%;
	}
	.info_box2>.count,.info_box2>.id{
		font-size: 13px;
		color: #999999;
	}
</style>


