<template>
	<div id="login">
		<div class="white_box"></div>
		<header>
			<div class="back" @click="$router.go(-1)"></div>
			<div class="title">欢迎来到厨圈</div>
			<div class="cart"></div>
		</header>
		<section>
			<div class="nav_box box">
				<div :class="{active:selected=='账号登录'}" @click="numLogIn()" class="numLogIn">账号登录</div>
				<div :class="{active:selected=='短信验证码登录'}" @click="telLogIn()" class="telLogIn">短信验证码登录</div>
			</div>
			<div v-if="selected == '账号登录'" class="login">
				<div class="box box1">
					<div>账号</div><input type="text" placeholder="手机号/邮箱" v-model='username'>
				</div>
				<div class="box box1">
					<div>密码</div><input type="password" v-model='password'>
				</div>
				<div class="clearfix">
					<div class="fl box">
						<div class="img_box"><img src="../assets/6-3dl_03.png" alt=""></div>
						<div>境外手机</div>
					</div>
					<div class="fr">
						<div @click="changePwd()">忘记密码</div>
					</div>
				</div>
			</div>
			<div v-if="selected == '短信验证码登录'" class="telLogIn2">
				<div class="box box1">
					<div>账号</div>
					<input type="text" placeholder="手机号" v-model='username'>
				</div>
				<div class="box box1">
					<div class="code_div">验证码</div>
					<input type="text" class="code_box" v-model='code'>
					<input type="button" value="获取验证码" class="code_btn" @click='getCode(this)' v-if="show">
					<span v-if="!show" class="time">{{count}}s</span>
				</div>
			</div>
		</section>
		<input type="button" value="登录" class="login_btn" @click="Login()">
		<div class="three_box">
			<div>第三方账号登录</div>
			<div class="box img_box2">
				<div><img src="../assets/login_btn_wx@2x.png" alt=""></div>
				<div><img src="../assets/login_btn_qq@2x.png" alt=""></div>
				<div><img src="../assets/login_btn_wb@2x.png" alt=""></div>
			</div>
		</div>
		<input type="button" value="快速注册" class="regist_btn" @click='Registe()'>
	</div>
</template>
<script>
	import { Toast } from 'mint-ui';
	export default{
		data(){
			return{
				selected:'账号登录',
				username:'',
				password:'',
				code:'',
				show: true,
				count: '',
				timer: null
			}
		},
		methods:{
			numLogIn(){
				this.selected = '账号登录'
			},
			telLogIn(){
				this.selected = '短信验证码登录'
			},
			Login(){
				// console.log(this.username);
				// console.log(this.userId);
				if (this.userId.length == 0) {
					Toast('请先注册账号');
				}else{
					for (var i = 0; i < this.userId.length; i++) {
						if (this.username != this.userId[i]) {
							Toast('请先注册账号');
						}else{
							if (this.uesername == this.userId[i] || this.password != this.userPwd[i]) {
								Toast('请输入正确的密码');
							}else{
								this.$router.push({path:'./Kit'});
								this.$store.dispatch('setLogin',true)
							}
						}
					}
				}
				
				// var email = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
				// var tel = /^1[3|4|5|8][0-9]\d{4,8}$/;
				// if (this.username == 0) {
				// 	Toast('请输入账号');
				// }else {
				// 	if (!email.test(this.username) && !tel.test(this.username)) {
				// 		Toast('请输入正确的账号');
				// 	}else if (this.password == 0 && this.code == 0) {
				// 		Toast('请输入完整');
				// 	}else {
				// 		this.$router.push({path:'./Kit'})
				// 	}
				// }
			},
			Registe(){
				this.$router.push({path:'./Registe'})
			},
			getCode(obj){
				if (!this.timer) {
					this.count = 60;
					this.show = false;
					this.timer = setInterval(() => {
						if (this.count > 0 && this.count <= 60) {
							this.count--;
						} else {
							this.show = true;
							clearInterval(this.timer);
							this.timer = null;
						}
					}, 1000)
				}
			},
			changePwd(){
				this.$router.push({path:'./ChangePwd'})
			}
		},
		computed:{
			userId:function(){
				return this.$store.getters.getUserId;
			},
			userPwd:function(){
				return this.$store.getters.getUserPwd;
			}
		}
	}
</script>
<style scoped>
	.white_box{
		height: 10px;
		position: fixed;
		width: 100%;
		top: 0;
		background-color: white;
		z-index: 100;
	}
	header{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		height: 50px;
		background-color: white;
		position: fixed;
		top: 10px;
		width: 98%;
		z-index: 100;
	}
	header>div{
		margin-top: 5px;
	}
	header .back{
		width: 40px;
		height: 40px;
		background-size: 40%;
		background-repeat: no-repeat;
		margin-top: 5px;
		background-image: url(../assets/nav_ht@2x.png);
	}
	header .title{
		font-size: 18px;
		height: 40px;
		color: #515151;
	}
	header .cart{
		width: 40px;
		height: 40px;
	}
	.box{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
	}
	.box1{
		flex-wrap: wrap;
	}
	section{
		margin-top: 70px;
	}
	.nav_box{
		margin-top: 20px;
	}
	section>.nav_box>div{
		width: 50%;
		text-align: center;
		position: relative;
		height: 35px;
		line-height: 35px;
	}
	.active{
		font-weight: bold;
	}
	.active:after{
		width: 60%;
		height: 2px;
		position: absolute;
		left: 20%;
		bottom: 0;
		content: '';
		background-color: #ffe24c;
	}
	.login,.telLogIn2{
		margin-top: 20px;
	}
	.box1>div{
		width: 14%;
		color: #1f1f1f; 
		height: 40px;
		line-height: 40px;
		font-size: 18px;
		margin-left: 1%;
	}
	.box1>input{
		width: 77%;
		border: none;
		outline: none;
		height: 40px;
		line-height: 40px;
		font-size: 18px;
	}
	.clearfix{
		margin-top: 20px;
		font-size: 13px;
		color: #747474;
	}
	.img_box>img{
		width: 15px;
		height: 15px;
	}
	.img_box{
		margin-right: 10px;
	}
	.login_btn{
		margin-top: 20px;
		width: 100%;
		height: 40px;
		background-color: #91e5c9;
		color: white;
		font-size: 18px;
		border-radius: 2px;
		border: none;
		outline: none;
	}
	.three_box{
		margin-top: 20px;
		text-align: center;
		color: #858585;
		font-size: 12px;
	}
	.three_box>.img_box2{
		width: 50%;
		margin: auto;
		margin-top: 10px;
	}
	.three_box>.img_box2>div{
		width: 33%;
		height: 60px;
	}
	.three_box>.img_box2>div>img{
		width: 100%;
		height: 100%;
	}
	.regist_btn{
		margin-top: 30px;
		width: 100%;
		height: 40px;
		background-color: white;
		color: #474747;
		font-size: 18px;
		border: rgb(43,43,43) solid 1px;
		border-radius: 3px;
		outline: none;
	}
	.box1>input.code_box{
		width: 45%
	}
	.box1>.code_div{
		width: 20%;
	}
	.box1>.code_btn{
		width: 30%;
		font-size: 13px;
		border-radius: 4px;
	}
	.time{
		text-align: center;
		background-color: #d8d8d8;
		width: 30%;
		height: 40px;
		line-height: 40px;
		font-size: 13px;
		border-radius: 4px;
	}
</style>


