<template>
	<div id="queation">
		<div class="white_box"></div>
		<header>
			<div class="back" @click="$router.go(-1)"></div>
			<div class="title">问答</div>
			<div class="cart"></div>
		</header>
		<section>
			<div class="slider_box">
				<mt-swipe :auto="4000">
					<mt-swipe-item><img src="../../static/2_1_09.png" alt=""></mt-swipe-item>
					<mt-swipe-item><img src="../../static/2_1_09.png" alt=""></mt-swipe-item>
					<mt-swipe-item><img src="../../static/2_1_09.png" alt=""></mt-swipe-item>
				</mt-swipe>
			</div>
			<div class="box list_box">
				<div @click = "toFirst()" :class="{active:selected == '1'}">最热</div>
				<div @click = "toSecond()" :class="{active:selected == '2'}">最新</div>
			</div>
			<div class="info_box">
				<div v-if="selected==1">
					<div v-for="item in hotList" class="info">
						<div class="q">
							{{item.q}}
						</div>
						<div class="a">
							{{item.a}}
						</div>
						<div class="n">
							{{item.n}} 赞同
						</div>
					</div>
				</div>
				<div v-if="selected==2">
					<div v-for="item in answerList" class="info">
						<div class="q2">
							{{item.q}}
						</div>
						<div class="n2">
							{{item.n}}个回答
						</div>
						<div v-if="item.aList.length != 0" v-for="item2 in item.aList">
							<div class="a2">
								·{{item2.a}}
							</div>
						</div>
					</div>
				</div>
			</div>
		</section>
	</div>
</template>
<script>
	export default{
		name:"question",
		data(){
			return{
				selected:'1',
				hotList:[],
				answerList:[]
			}
		},
		methods:{
			toFirst(){
				this.selected = 1
			},
			toSecond(){
				this.selected = 2
			},
			getHotList(){
				this.$http.get('./static/question.json')
				.then(response=>{
					this.hotList = response.data.hotList
				})
			},
			getanswerList(){
				this.$http.get('./static/question.json')
				.then(response=>{
					this.answerList = response.data.answerList
				})
			}
		},
		created(){
			this.getHotList(),
			this.getanswerList()
		}
	}
</script>
<style scoped>
	.white_box{
		height: 10px;
		position: fixed;
		width: 100%;
		top: 0;
		background-color: white;
		z-index: 100;
	}
	header{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		height: 50px;
		background-color: white;
		position: fixed;
		top: 10px;
		width: 98%;
		z-index: 100;
	}
	header>div{
		margin-top: 5px;
	}
	header .back{
		width: 40px;
		height: 40px;
		background-size: 40%;
		background-repeat: no-repeat;
		margin-top: 5px;
		background-image: url(../assets/nav_ht@2x.png);
	}
	header .title{
		font-size: 18px;
		height: 40px;
		color: #515151;
	}
	header .cart{
		width: 40px;
		height: 40px;
		color: red;
	}
	.box{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
	}
	section{
		margin-top: 70px;
	}
	.slider_box{
		height: 120px;
		width: 100%;
		margin-top: 20px;
	}
	.slider_box img{
		width: 100%;
	}
	.list_box>div{
		width: 48%;
		line-height: 35px;
		font-size: 14px;
		text-align: center;
		position: relative;
	}
	.active{
		color: red;
	}
	.active:after{
		width: 80%;
		height: 2px;
		position: absolute;
		left: 10%;
		bottom: 0;
		content: '';
		background-color: red;
	}
	.info{
		margin: 20px;
		border-bottom: 1px solid #333333;
	}
	.info>.q,.info>.q2{
		font-size: 15px;
	}
	.info>.a,.info>.n{
		font-size: 13px;
		color: #333333;
		margin-top: 10px;
		margin-bottom: 10px;
	}
	.info .a2,.info .n2{
		font-size: 13px;
		color: #333333;
		margin-top: 10px;
		margin-bottom: 10px;
	}
</style>


