<template>
	<div id="detail">
		<div class="white_box"></div>
		<header class="clearfix">
			<div class="back" @click="$router.go(-1)"></div>
			<div class="title">详情</div>
			<div class="cart"  @click="goToCart()"></div>
		</header>
		<section>
			<img :src="this.detail1.img" alt="">
			<div class="name">{{this.detail1.name}}</div>
			<ul>
				<li v-for="item in this.detail1.step">{{item.step}}</li>
			</ul>
			<div class="place">{{this.detail1.place}}</div>
			<div class="des">{{this.detail1.des}}</div>
			<div class="info_box clearfix" v-if="this.detail1.class2 == 0">
				<div class="price fl">￥{{this.detail1.price}}</div>
				<div class="mail fl">{{this.detail1.mail}}</div>
				<div class="sale fr">已售{{this.detail1.sale}}</div>
			</div>
			<div class="btn_box" v-if="this.detail1.class2 == 0">
				<input type="button" value="加入购物车" class="btn1" @click="addCart(detail1)">
			</div>
		</section>
	</div>
</template>
<script>
	import { Toast } from 'mint-ui';
	export default{
		name:"detail",
		data(){
			return{
				isshow:false,
				id:''
			}
		},
		methods:{
			buyNow(info){
				this.isshow = true;
				// console.log(info);
				this.id = info;
			},
			addCart(info){
				Toast('添加购物车成功');
				// console.log(info)
				this.$store.dispatch('setCart',info)
			},
			goToCart(){
				this.$router.push({path:'/Cart'})
			}
		},
		computed:{
			detail1:function(){
				return this.$store.getters.getDetail1;
			}
		}
	}
</script>
<style scoped>
	.white_box{
		height: 10px;
		position: fixed;
		width: 100%;
		top: 0;
		background-color: white;
		z-index: 100;
	}
	header{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		height: 50px;
		background-color: white;
		position: fixed;
		top: 10px;
		width: 98%;
		z-index: 100;
	}
	header>div{
		margin-top: 5px;
	}
	header .back{
		width: 40px;
		height: 40px;
		background-size: 40%;
		background-repeat: no-repeat;
		margin-top: 5px;
		background-image: url(../assets/nav_ht@2x.png);
	}
	header .title{
		font-size: 18px;
		height: 40px;
		color: #515151;
	}
	header .cart{
		width: 40px;
		height: 40px;
		background-size: 30px 30px;
		background-repeat: no-repeat;
		margin-top: 5px;
		background-image: url(../assets/nav_gwc@2x.png);
	}
	.box{
		display: flex;
		justify-content: space-between;
	}
	section{
		margin-top: 70px;
	}
	img{
		width: 100%;
	}
	.name{
		width: 100%;
		text-align: center;
		color: red;
		font-size: 18px;
		margin-top: 20px;
	}
	li{
		margin-top: 10px;
		color: #999999;
	}
	.place{
		width: 100%;
		text-align: center;
	}
	.info_box{
		margin-top: 20px;
		line-height: 25px;
	}
	.mail{
		margin-left: 50px;
	}
	input{
		margin-top: 20px;
		width: 98%;
		height: 25px;
		border: none;
		outline: none;
		border-radius: 4px;
	}
	.btn1{
		background-color: #ebc901;
	}
	.btn2{
		background-color: #ea2000;
	}
	.close{
		position: absolute;
		right: 5px;
		top:0;
	}
	.buy{
		width: 100%;
		margin-top: 30px;
	}
	.price{
		color: red;
		font-size: 20px;
	}
</style>


