<template>
	<div id="setting">
		<div class="white_box"></div>
		<header class="clearfix">
			<div class="back" @click="$router.go(-1)"></div>
			<div class="title">设置</div>
			<div class="cart"></div>
		</header>
		<section>
			<div>账户绑定与设置</div>
			<div>常用信息设置<span class="fr">菜谱信息、常用地址</span></div>
			<div>通知设置</div>
			<div>清除缓存</div>
			<br>
			<div>常见问题与反馈</div>
			<div>推荐给好友</div>
			<div>支持我们 打分评价</div>
			<div>关于厨圈</div>
		</section>
		<input type="button" value="退出当前账号">
	</div>
</template>
<script>
	export default{
		name:"setting",
		data(){
			return{

			}
		}
	}
</script>
<style scoped>
	#setting{
		background-color: rgb(248,248,248);
	}
	.box{
		display: flex;
		justify-content: space-between;
	}
	.white_box{
		height: 10px;
		position: fixed;
		width: 100%;
		top: 0;
		background-color: white;
		z-index: 100;
	}
	header{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		height: 50px;
		background-color: white;
		position: fixed;
		top: 10px;
		width: 98%;
		z-index: 100;
	}
	header>div{
		margin-top: 5px;
	}
	header .back{
		width: 40px;
		height: 40px;
		background-size: 40%;
		background-repeat: no-repeat;
		background-image: url(../assets/nav_ht@2x.png);
	}
	header .title{
		font-size: 18px;
		height: 40px;
		color: #515151;
	}
	header .cart{
		width: 40px;
		height: 40px;
	}
	section{
		margin-top: 70px;
	}
	section>div{
		height: 60px;
		line-height: 60px;
		color: #474747;
		font-size: 14px;
		background-color: white;
		border-bottom: 1px solid rgb(248,248,248);
		background: url(../assets/6-2sz_03.png) no-repeat 96% 50%;
		background-size: 8px 16px;
	}
	section>div>span{
		font-size: 12px;
		margin-right: 10%;
		color: #999999;
	}
	input{
		width: 100%;
		height: 60px;
		border: none;
		outline: none;
		background-color: white;
		color: #49adf3;
		font-size: 18px;
		margin-top: 10px;
	}
</style>


