<template>
	<div id="cart">
		<div class="white_box"></div>
		<header>
			<div class="back" @click="$router.go(-1)"></div>
			<div class="title">购物车</div>
			<div class="cart"></div>
		</header>
		<section>
			<!-- <div v-if="this.total.length == 0">购物车为空</div> -->
			<div class="box shop_box" v-for="item in cartList">
				<input type="checkbox" @click="getinfo(item)" :checked="item.checked">
				<img :src="item.img" alt="">
				<div class="info_box clearfix">
					<div>{{item.des}}</div>
					<div class="name">{{item.name}}</div>
					<div class="price fl">￥{{item.price}}</div>
					<div class="fr clearfix count_box">
						<div class="fl btn btn1" @click="reduce(item)">-</div>
						<span class="count" >x{{item.count}}</span>
						<div class="fr btn btn2" @click="add(item)">+</div>
					</div>
				</div>
			</div>
			<div class="empty_box">
			</div>
			<div class="settle">
				<input type="checkbox" class="check" @click="getAll()" :checked="checked2">
				<span>全选</span>
				<span class="totalMoney">合计：￥{{this.money}}</span>
				<span class="totalCount fr" @click="pay()">结算({{this.total.length}})</span>
			</div>
		</section>
	</div>
</template>
<script>
	import { Toast } from 'mint-ui';
	export default{
		name:"cart",
		data(){
			return{
				cartList:[],
				total:[],
				money:0,
				checked:false,
				checked2:false
			}
		},
		methods:{
			getCartList(){
				this.$http.get('./static/cartList.json')
				.then(response=>{
					this.cartList = response.data.cartList;
					var newP = this.$store.getters.getCart;
					// console.log(newP);
					// console.log(this.cartList)
					for (var i = 0; i < newP.length; i++) {
						this.cartList.push(newP[i]);
						// console.log('购物单')
						// console.log(this.cartList)
					}
				})
			},
			getinfo(info){
				// console.log(info.checked)
				if (info.checked == false) {
					// console.log('选中')
					// console.log(info);
					info.checked = true
					// console.log('准备购买')
					// console.log(this.total);
					if (this.total.length == 0) {
						this.total.push(info)
						// console.log('选中-空车')
						this.money = this.money+info.price*info.count
					}else{
						// console.log('选中-非空车')
						if (this.checked == false ) {
							// console.log('选中-非空车-未全选')
							this.total.push(info)
							this.money = this.money+info.price*info.count
						}else{
							for (var i = 0; i < this.total.length; i++) {
								// console.log('选中-非空车-全选')
								if (this.total[i].name == info.name) {
									// console.log('选中-非空车-全选-存在')
									// console.log(this.total)
									// console.log('')
									this.money = this.money-this.total[i].price*this.total[i].count
									this.total.splice(i,1)
									// console.log(this.total)
									return
								}else{
									// console.log('选中-非空车-全选-不存在')
									this.total.push(info)
									this.money = this.money+info.price*info.count
									// console.log(this.total)
									return
								}
							}
						}
					}
				}else{
					// console.log('取消')
					info.checked = false
					// console.log(info);
					for (var i = 0; i < this.total.length; i++) {
						if (this.total[i].name == info.name) {
							this.money = this.money-this.total[i].price*this.total[i].count
							this.total.splice(i,1);
							// console.log('已从购物车中删除')
							for (var i = 0; i < this.total.length; i++) {
								// console.log('剩下'+this.total[i].name)
							}
						}
					}
				}
			},
			getAll(){
				if (this.checked == false) {
					this.checked = true;
					this.checked2 = true;
					// console.log(this.cartList)
					this.total = [];
					this.money = 0;
					for (var i = 0; i < this.cartList.length; i++) {
						this.cartList[i].checked = true;
						this.total.push(this.cartList[i]);
					}
					// console.log(this.total)
					for (var j = 0; j < this.total.length; j++) {
						this.money = this.money+this.total[j].price*this.total[j].count
					}
				}else{
					this.checked = false;
					this.checked2 = false;
					for (var i = 0; i < this.cartList.length; i++) {
						this.cartList[i].checked = false;
						this.total = [];
						this.money = 0;
					}
				}
			},
			reduce(info){
				if (info.count>1) {
					info.count--
					for (var i = 0; i < this.total.length; i++) {
						if (this.total[i].name == info.name) {
							this.money = this.money-info.price
						}
					}
				}
			},
			add(info){
				info.count++
				if (this.total.length == 0) {
					return
				}else{
					for (var i = 0; i < this.total.length; i++) {
						if (this.total[i].name == info.name) {
							this.money = this.money+info.price
						}
					}
				}
			},
			pay(){
				// console.log(this.total);
				// console.log(this.cartList);
				for (var i = 0; i < this.cartList.length; i++) {
					if (this.cartList[i].checked == true) {
						this.cartList[i].checked = false;
						this.cartList.splice(i,1);
						i=-1
					}
				}
				// this.cartList = '';
				this.money = 0;
				this.total.length = 0;
				this.checked2 = false;
				Toast('购买成功');
				// console.log(this.cartList)
			}
		},
		created(){
			this.getCartList()
		}
	}
</script>
<style scoped>
	.empty_box{
		height: 80px;
	}
	.white_box{
		height: 10px;
		position: fixed;
		width: 100%;
		top: 0;
		background-color: white;
		z-index: 100;
	}
	header{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		height: 50px;
		background-color: white;
		position: fixed;
		top: 10px;
		width: 98%;
		z-index: 100;
	}
	header>div{
		margin-top: 5px;
	}
	header .back{
		width: 40px;
		height: 40px;
		background-size: 40%;
		background-repeat: no-repeat;
		margin-top: 5px;
		background-image: url(../assets/nav_ht@2x.png);
	}
	header .title{
		font-size: 18px;
		height: 40px;
		color: #515151;
	}
	header .cart{
		width: 40px;
		height: 40px;
		color: red;
	}
	.box{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
	}
	section{
		margin-top: 70px;
	}
	.shop_box{
		margin-top: 10px;
	}
	.shop_box>img{
		width: 100px;
		height: 100px;
	}
	.shop_box>input{
		margin-top: 45px;
	}
	.shop_box>.info_box{
		width: 60%;
		font-size: 14px;
	}
	.name{
		color: #999999;
		margin-top: 10px;
	}
	.price{
		margin-top: 10px;
		color: red;
	}
	.count_box{
		margin-top: 10px;
		line-height: 26px;
	}
	.settle{
		width: 98%;
		height: 50px;
		line-height: 50px;
		position: fixed;
		bottom: 0;
		left: 1%;
		background-color: white;
	}
	.settle>.check{
		margin-left: 7px;
	}
	.totalMoney{
		color: rgb(246,80,80);
		margin-left: 10%;
	}
	.totalCount{
		margin-right: 10px;
		margin-top: 5px;
		background-color: rgb(246,80,80);
		color: white;
		height: 40px;
		width: 70px;
		text-align: center;
		line-height: 40px;
	}
	.btn{
		font-size: 20px
	}
	.btn1{
		margin-right: 5px;
	}
	.btn2{
		margin-left: 5px;
	}
</style>


