<template>
	<div id="registe">
		<div class="white_box"></div>
		<header class="clearfix">
			<div class="back" @click="$router.go(-1)"></div>
			<div class="title">注册账号</div>
			<div class="cart"></div>
		</header>
		<section>
			<div class="nav_box box">
				<div :class="{active:selected=='手机号注册'}" @click="numLogIn()" class="numLogIn">手机号注册</div>
				<div :class="{active:selected=='个性化账号'}" @click="telLogIn()" class="telLogIn">个性化账号</div>
			</div>
			<div v-if="selected == '手机号注册'">
				<div class="number_box box">
					<select>
						<option value="" v-for='item in countryNumList'>{{item.country}} + {{item.num}}</option>
					</select>
					<input type="text" placeholder="输入手机号" v-model="telNum">
				</div>
				<div class="code_tip box">
					<div class="code_div">验证码</div>
					<input type="text" class="code_box" v-model='code'>
					<input type="button" value="获取验证码" class="code_btn" @click='getCode(this)' v-if="show">
					<span v-if="!show" class="time">{{count}}s</span>
				</div>
				<div class="name_box box">
					<div class="name_div">输入昵称</div>
					<input type="text" v-model="username">
				</div>
				<div class="pwd_box box">
					<div class="pwd_div">设置密码</div>
					<input type="password" v-model="password">
				</div>
				<div class="tip">点击注册即同意<span>《用户注册协议》</span></div>
				<input type="button" value="注册" class="regist_btn" @click="registe()">
			</div>
			<div v-if="selected == '个性化账号'">
				<div class="number_box box">
					<select>
						<option value="" v-for='item in countryNumList'>{{item.country}} + {{item.num}}</option>
					</select>
					<input type="text" placeholder="输入手机号" v-model="telNum">
				</div>
				<div class="code_tip box">
					<div class="code_div">验证码</div>
					<input type="text" class="code_box" v-model='code'>
					<input type="button" value="获取验证码" class="code_btn" @click='getCode(this)' v-if="show">
					<span v-if="!show" class="time">{{count}}s</span>
				</div>
				<div class="name_box box">
					<div class="name_div">输入昵称</div>
					<input type="text" v-model="username">
				</div>
				<div class="pwd_box box">
					<div class="pwd_div">设置密码</div>
					<input type="password" v-model="password">
				</div>
				<div class="tip">点击注册即同意<span>《用户注册协议》</span></div>
				<input type="button" value="注册" class="regist_btn" @click="registe()">
			</div>
		</section>
	</div>
</template>
<script>
	import { Toast } from 'mint-ui';
	export default{
		data(){
			name:"registe";
			return{
				selected:'手机号注册',
				countryNumList:[],
				telNum:"",
				code:'',
				username:'',
				password:'',
				show: true,
				count: '',
				timer: null
			}
		},
		methods:{
			numLogIn(){
				this.selected = '手机号注册'
			},
			telLogIn(){
				this.selected = '个性化账号'
			},
			getCountryNum(){
				this.$http.get("./static/countryNumList.json")
				.then(response=>{
					this.countryNumList = response.data.countryNum;
					// console.log(this.countryNumList)
				})
			},
			getCode(obj){
				if (!this.timer) {
					this.count = 60;
					this.show = false;
					this.timer = setInterval(() => {
						if (this.count > 0 && this.count <= 60) {
							this.count--;
						} else {
							this.show = true;
							clearInterval(this.timer);
							this.timer = null;
						}
					}, 1000)
				}
			},
			registe(){
				if (this.telNum == 0) {
					Toast('请输入手机号');
				}else {
					if (this.telNum.length < 11) {
						Toast('请输入正确的手机号');
					}else if (this.code == 0 || this.username == 0 ||  this.password == 0) {
						Toast('请填写完整');
					}else {
						// console.log(this.telNum);
						// console.log(this.password);
						this.$store.dispatch('setUserId',this.telNum);
						this.$store.dispatch('setUserPwd',this.password);
						this.telNum='';
						this.code='';
						this.username='';
						this.password='';
						Toast('注册成功');
						this.$router.push({path:'./Login'})
						// this.$store.dispatch('setLogin',true);
						// this.$router.push({path:'./kit'})
					}
				}
			}
		},
		created(){
			this.getCountryNum()
		}
	}
</script>
<style scoped>
	.white_box{
		height: 10px;
		position: fixed;
		width: 100%;
		top: 0;
		background-color: white;
		z-index: 100;
	}
	header{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		height: 50px;
		background-color: white;
		position: fixed;
		top: 10px;
		width: 98%;
		z-index: 100;
	}
	header>div{
		margin-top: 5px;
	}
	header .back{
		width: 40px;
		height: 40px;
		background-size: 40%;
		background-repeat: no-repeat;
		margin-top: 5px;
		background-image: url(../assets/nav_ht@2x.png);
	}
	header .title{
		font-size: 18px;
		height: 40px;
		color: #515151;
	}
	header .cart{
		width: 40px;
		height: 40px;
	}
	section{
		margin-top: 70px;
	}
	.box{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
	}
	section>.nav_box>div{
		width: 50%;
		text-align: center;
		position: relative;
		height: 35px;
		line-height: 35px;
	}
	.active{
		font-weight: bold;
	}
	.active:after{
		width: 60%;
		height: 2px;
		position: absolute;
		left: 20%;
		bottom: 0;
		content: '';
		background-color: #ffe24c;
	}
	.number_box{
		margin-top: 20px;
	}
	.number_box>select{
		width: 30%;
		height: 40px;
		border: none;
		color: #54a0ec;
		font-size: 15px;
		outline: none;
	}
	.number_box>input{
		width: 68%;
		height: 40px;
		border: none;
		font-size: 15px;
		outline: none;
	}
	.code_tip>div,.code_tip>input,.code_tip>span{
		height: 40px;
		line-height: 40px;
		font-size: 15px;
	}
	.code_div{
		width: 20%;
		color: #505050;
	}
	.code_box{
		width: 30%;
		outline: none;
		border: none;
		margin-left: 3%;
	}
	.code_btn{
		width: 30%;
		font-size: 13px;
		border-radius: 4px;
		border: none;
		outline: none;
		background-color: #fae268;
	}
	.time{
		text-align: center;
		background-color: #fae268;
		width: 30%;
		height: 40px;
		line-height: 40px;
		font-size: 13px;
		border-radius: 4px;
	}
	.name_box,.pwd_box{
		height: 40px;
		line-height: 40px;
		margin-top: 20px;
		color: #505050;
		font-size: 15px;
	}
	.name_box>input,.pwd_box>input{
		outline: none;
		border: none;
		width: 69%;
		font-size: 15px;
	}
	.tip{
		margin-top: 20px;
		font-size: 10px;
		color: #505050;
	}
	.tip>span{
		color: #83d1f2;
	}
	.regist_btn{
		margin-top: 20px;
		width: 100%;
		height: 40px;
		border: none;
		outline: none;
		background-color: #91e5c9;
		color: white;
	}
</style>


