<template>
	<div id="kitchen">
		<section>
			<mt-tabbar v-model="selected" class="mt_btn">
				<mt-tab-item id="kitchen">
					<img src="../assets/tab_cf@2x.png">
					<div>厨房</div>
				</mt-tab-item>
				<mt-tab-item id="mall">
					<img src="../assets/tab_sc@2x.png">
					<div>商场</div>
				</mt-tab-item>
				<mt-tab-item id="class">
					<img src="../assets/tab_fl@2x.png">
					<div>分类</div>
				</mt-tab-item>
				<mt-tab-item id="square">
					<img src="../assets/tab_gc@2x.png">
					<div>广场</div>
				</mt-tab-item>
				<mt-tab-item id="mine">
					<img src="../assets/tab_wd@2x.png">
					<div>我的</div>
				</mt-tab-item>
			</mt-tabbar>
			<div v-if="selected == 'kitchen'">
				<div class="white_box"></div>
				<header class="clearfix">
					<div class="add"></div>
					<div class="search_box box">
						<div class="search_logo"></div>
						<input type="text" placeholder="搜索菜谱、食材">
						<div class="mic_logo"></div>
					</div>
					<div class="cart"  @click="goToCart()"></div>
				</header>
				<div class="nav_box box">
					<div class="_box" @click="goToGoodThing()">
						<img src="../assets/home_btn_cfhw@2x.png" alt=""><div>厨房好物</div>
					</div>
					<div class="_box"  @click="goToQuestion()">
						<img src="../assets/home_btn_cfwd@2x.png" alt=""><div>厨房问答</div>
					</div>
					<div class="_box" @click="goToRankList()">
						<img src="../assets/home_btn_phb@2x.png" alt=""><div>排行榜</div>
					</div>
					<div class="_box" @click="goToSign()">
						<img src="../assets/home_btn_jksp@2x.png" alt=""><div>活动签到</div>
					</div>
				</div>
				<div class="sign_box box" v-if="isshow == true">
					<input type="button" value="签到" @click="sign()"  class="sign_btn">
				</div>
				<div class="tip_box box">
					<div class="menu" @click="goToMenu()"><div><span>本周</span><br><span>流行菜谱</span></div></div>
					<div class="lesson" @click="goToLesson()"><div><span>在线</span><br><span>美食课堂</span></div></div>
				</div>
				<div class="slider_box">
					<mt-swipe :auto="4000">
						<mt-swipe-item><img src="../../static/2_1_09.png" alt=""></mt-swipe-item>
						<mt-swipe-item><img src="../../static/2_1_09.png" alt=""></mt-swipe-item>
						<mt-swipe-item><img src="../../static/2_1_09.png" alt=""></mt-swipe-item>
					</mt-swipe>
				</div>
				<div class="img_box box">
					<img src="../../static/2_1_13.png" alt="">
				</div>
				<div class="lesson_box clearfix" style="height:228px;overflow-y:hidden">
					<div class="tip">
						<span class="fl cook_lesson">厨课堂</span>
						<span class="fr look_all"></span>
					</div>
					<div class="lessons">
						<ul class="clearfix" :style="{width:160*lessonList.length+'px'}">
							<li v-for="item in lessonList" class="fl" @click="toDetail1(item)">
								<div class="lessonImg"><img :src="item.img" alt=""></div>
								<div class="lessonName">{{item.name}}</div>
							</li>
						</ul>
					</div>
				</div>
			</div>
			<div v-if="selected == 'mall'">
				<div class="white_box"></div>
				<header class="clearfix">
					<div class="location"><div>扬州市</div></div>
					<div class="search_box box">
						<div class="search_logo"></div>
						<input type="text" placeholder="搜索菜谱、食材">
						<div class="mic_logo"></div>
					</div>
					<div class="cart"  @click="goToCart()"></div>
				</header>
				<div class="nav_box2 box">
					<div @click="toBake()">
						<div class="img_box2"><img src="../assets/home_btn_hb@2x.png" alt=""></div>
						<div class="text">烘焙</div>
					</div>
					<div @click="toVegetable()">
						<div class="img_box2"><img src="../assets/home_btn_gssx@2x.png" alt=""></div>
						<div class="text">果蔬生鲜</div>
					</div>
					<div @click="toTool()">
						<div class="img_box2"><img src="../assets/home_btn_qj@2x.png" alt=""></div>
						<div class="text">器具</div>
					</div>
					<!-- <div @click="toCoupon()">
						<div class="img_box2"><img src="../assets/home_btn_lq@2x.png" alt=""></div>
						<div class="text">领券</div>
					</div> -->
					<div @click="toFast()">
						<div class="img_box2"><img src="../assets/home_btn_fbsp@2x.png" alt=""></div>
						<div class="text">方便食品</div>
					</div>
					<div @click="toIn()">
						<div class="img_box2"><img src="../assets/home_btn_jksp@2x.png" alt=""></div>
						<div class="text">进口食品</div>
					</div>
					<div @click="toVegetable()">
						<div class="img_box2"><img src="../assets/home_btn_slxs@2x.png" alt=""></div>
						<div class="text">时令鲜食</div>
					</div>
					<!-- <div @click="toALL()">
						<div class="img_box2"><img src="../assets/home_btn_qbfl@2x.png" alt=""></div>
						<div class="text">全部分类</div>
					</div> -->
				</div>
				<div class="slider_box2">
					<mt-swipe :auto="4000">
						<mt-swipe-item><img src="../../static/mallImg/mall_01.png" alt=""></mt-swipe-item>
						<mt-swipe-item><img src="../../static/mallImg/mall_01.png" alt=""></mt-swipe-item>
						<mt-swipe-item><img src="../../static/mallImg/mall_01.png" alt=""></mt-swipe-item>
					</mt-swipe>
				</div>
				<hr>
				<div class="mall_box" style="height:240px;overflow-y:hidden">
					<div class="tip2">好店推荐</div>
					<div class="mall2" >
						<ul class="clearfix" :style="{width:160*mallList.length+'px'}">
							<li v-for="item in mallList" class="fl" @click="toDetail1(item)">
								<div class="lessonImg"><img :src="item.img" alt=""></div>
								<div class="lessonName">{{item.name}}</div>
							</li>
						</ul>
					</div>
				</div>
			</div>
			<div v-if="selected == 'class'">
				<div class="white_box"></div>
				<header class="clearfix">
					<div class="location"><div>扬州市</div></div>
					<div class="search_box box">
						<div class="search_logo"></div>
						<input type="text" placeholder="搜索菜谱、食材">
						<div class="mic_logo"></div>
					</div>
					<div class="cart_empty"></div>
					<!-- <div class="cart"  @click="goToCart()"></div> -->
				</header>
				<div class="select_box clearfix">
					<div class="select_tip">精选</div>
					<div class="select_img_box box">
						<div class="box1">
							<img src="../../static/classImg/class_06.png" alt="">
							<div class="select_logo" @click="goToMenu()">
								<img src="../../static/classImg/class_01.png" alt=""><span>达人菜谱</span>
								<div>美食达人优质菜谱</div>
							</div>
						</div>
						<div class="box2">
							<img src="../../static/classImg/class_07.png" alt="">
							<div class="select_logo" @click="goToMenu()">
								<img src="../../static/classImg/class_02.png" alt=""><span>独家菜谱</span>
								<div>精选独家优质菜谱</div>
							</div>
						</div>
					</div>
				</div>
				<div class="video_box clearfix">
					<div class="video_tip">视频</div>
					<div class="video_img_box box">
						<div class="box1"  @click="goToLesson()">
							<img src="../../static/classImg/class_10.png" alt="">
							<div class="select_logo">
								<img src="../../static/classImg/class_03.png" alt=""><span>视频菜谱</span>
								<div>大厨视频传授私房秘诀</div>
							</div>
						</div>
					</div>
				</div>
				<div class="recommend_box clearfix">
					<div class="recommend_tip">推荐</div>
					<div class="recommend_img_box box">
						<div class="box1" @click="toVideo()">
							<img src="../../static/classImg/class_08.png" alt="">
							<div class="select_logo">
								<img src="../../static/classImg/class_04.png" alt=""><span>周最热</span>
								<div>本周流行菜谱</div>
							</div>
						</div>
						<div class="box2" @click="toVegetable()">
							<img src="../../static/classImg/class_09.png" alt="">
							<div class="select_logo">
								<img src="../../static/classImg/class_05.png" alt=""><span>新鲜菜</span>
								<div>24小时实时推荐</div>
							</div>
						</div>
					</div>
				</div>
			</div>
			<div v-if="selected == 'square'">
				<div class="white_box"></div>
				<header class="clearfix">
					<div class="location"><div>扬州市</div></div>
					<div class="select_title">厨房精选</div>
					<div class="cart"  @click="goToCart()"></div>
				</header>
				<div class="slider_box3">
					<mt-swipe :auto="4000">
						<mt-swipe-item><img src="../../static/squareImg/square_01.png" alt=""></mt-swipe-item>
						<mt-swipe-item><img src="../../static/squareImg/square_01.png" alt=""></mt-swipe-item>
						<mt-swipe-item><img src="../../static/squareImg/square_01.png" alt=""></mt-swipe-item>
					</mt-swipe>
				</div>
				<div class="tab_box">
					<div class="btn_box box">
						<div v-for='item in itemList' @click='toOther(item)' :class="{active:selected2==item}">{{item}}</div>
					</div>
					<div class="dynamic_box">
						<div v-if="active == '关注'" class="dynamic_gz">
							<div v-for="item in dynamicList" v-if="item.follow == 1">
								<img class="headImg" :src="item.headImg" alt="">
								<div class="box3">
									<span class="name">{{item.userName}}</span>
									<span class="time">{{item.time}}说</span>
								</div>
								<div class="title">{{item.title}}</div>
								<div class="text">{{item.text}}</div>
								<div class="img_box3 box" >
									<div v-for="item2 in item.imgList"><img :src="item2.img" alt=""></div>
								</div>
								<div class="btn_box3 box">
									<div class="share">{{item.share}}</div>
									<img :src="item.bgImg" alt="" @click='favor(item)'>
									<div class="favor" @click='favor(item)'>{{item.favor}}</div>
									<div class="comment">{{item.comment}}</div>
								</div>
							</div>
						</div>
						<div v-if="active == '广场'" class="dynamic_gc">
							<div v-for="item in dynamicList">
								<img class="headImg" :src="item.headImg" alt="">
								<div class="box3">
									<span class="name">{{item.userName}}</span>
									<span class="time">{{item.time}}说</span>
								</div>
								<div class="title">{{item.title}}</div>
								<div class="text">{{item.text}}</div>
								<div class="img_box3 box">
									<div v-for="item2 in item.imgList"><img :src="item2.img" alt=""></div>
								</div>
								<div class="btn_box3 box">
									<div class="share"></div>
									<img :src="item.bgImg" alt="" @click='favor(item)'>
									<div class="favor" @click='favor(item)'>{{item.favor}}</div>
									<div class="comment">{{item.comment}}</div>
								</div>
							</div>
						</div>
						<div v-if="active == '话题'">
							<div class="btn_box box2">
								<div @click='toRecommend()' :class="{active2:selected3=='推荐'}">推荐</div>
								<div @click='toNew()' :class="{active2:selected3=='最新'}">最新</div>
								<div @click='toHot()' :class="{active2:selected3=='最热'}">最热</div>
							</div>
							<div v-if="active2 == '推荐'">
								<div v-for="item in topicList" class="box img_box4">
									<img :src="item.img" alt="">
									<div class="tip_box4">
										<div class="title">{{item.title}}</div>
										<div class="des">#{{item.des}}#</div>
										<span class="text">{{item.text}}</span>
									</div>
								</div>
							</div>
							<div v-if="active2 == '最新'">
								<div v-for="item in topicList" class="box img_box4">
									<img :src="item.img" alt="">
									<div class="tip_box4">
										<div class="title">{{item.title}}</div>
										<div class="des">#{{item.des}}#</div>
										<span class="text">{{item.text}}</span>
									</div>
								</div>
							</div>
							<div v-if="active2 == '最热'">
								<div v-for="item in topicList" class="box img_box4">
									<img :src="item.img" alt="">
									<div class="tip_box4">
										<div class="title">{{item.title}}</div>
										<div class="des">#{{item.des}}#</div>
										<span class="text">{{item.text}}</span>
									</div>
								</div>
							</div>
						</div>
						<div v-if="active == '问答'">
							<div v-for='item in qaList' class="qa_box">
								<div>问：“{{item.q}}”</div>
								<div>答：“{{item.a}}”</div>
							</div>
						</div>
					</div>
				</div>
			</div>
			<div v-if="selected == 'mine'">
				<div class="white_box"></div>
				<header class="clearfix">
					<div class="location1"></div>
					<div class="select_title">我的</div>
					<div class="set"  @click="goToSetting()"></div>
				</header>
				<div class="login_box clearfix" v-if='this.login == false'>
					<div class="head"><img src="../assets/unloghead.png" alt=""></div>
					<div class="userName" @click="logIn()">请登录</div>
				</div>
				<div class="login_box clearfix" v-if='login == true'>
					<div class="head2"><img :src="img" alt=""></div>
					<div class="userName">{{userName}}</div>
				</div>
				<div class="money_box box">
					<div class="wallet">
						<div><img src="../assets/home_btn_qb1@2x.png" alt=""><span>钱包</span></div>
						<div class="count">0.00元</div>
					</div>
					<div class="golden">
						<div><img src="../assets/home_btn_jb@2x.png" alt=""><span>金币</span></div>
						<div class="count">0.00元</div>
					</div>
				</div>
				<div class="more_box box">
					<!-- <div>
						<img src="../assets/home_btn_rw@2x.png" alt="">
						<div>任务</div>
					</div> -->
					<div @click="toOrder()">
						<img src="../assets/home_btn_dd@2x.png" alt="">
						<div>订单</div>
					</div>
					<div @click="goToCart()">
						<img src="../assets/home_btn_gwc@2x.png" alt="">
						<div>购物车</div>
					</div>
					<div @click="toCollection()">
						<img src="../assets/home_btn_sc@2x.png" alt="">
						<div>收藏</div>
					</div>
					<div @click="toFoot()">
						<img src="../assets/home_btn_zj@2x.png" alt="">
						<div>足迹</div>
					</div>
					<div @click="toQA()">
						<img src="../assets/home_btn_wd@2x.png" alt="">
						<div>问答</div>
					</div>
					<!-- <div>
						<img src="../assets/home_btn_qb@2x.png" alt="">
						<div>券包</div>
					</div> -->
					<div @click="toService()">
						<img src="../assets/home_btn_kf@2x.png" alt="">
						<div>客服</div>
					</div>
				</div>
			</div>
		</section>
	</div>
</template>
<script>
	import { Toast } from 'mint-ui';
	export default{
		name:'ketchen',
		data(){
			return{
				lessonList:[],//厨课堂信息
				mallList:[],
				selected:'kitchen',
				active:"关注",
				userName:'yige壹',
				img:'../dist/static/headImg/head_01.jpg',
				itemList:[
					"关注",
					'广场',
					'话题',
					'问答'
				],
				selected2:'关注',
				dynamicList:[],
				itemList2:[
					"推荐",
					"最新",
					"最热"
				],
				selected3:'推荐',
				active2:'推荐',
				topicList:[],
				qaList:[],
				isshow:false,
				count2:false,
				favored:false
			}
		},
		methods:{
			getData(){
				// 获取厨课堂信息
				this.$http.get('./static/lessonList.json')
				.then(response=>{
					this.lessonList = response.data.lessonList;
					// console.log(response);
				})
			},
			goToMenu(){
				this.$router.push({path:'/Menu'})
				// console.log(666)
			},
			goToLesson(){
				this.$router.push({path:'/Lesson'})
			},
			goToGoodThing(){
				this.$router.push({path:'/GoodThing'})
			},
			goToQuestion(){
				this.$router.push({path:'./Question'})
			},
			goToRankList(){
				this.$router.push({path:'./Rank'})
			},
			goToSign(){
				// console.log(this.isshow)
				if (this.count2 == false) {
					this.isshow = true
				}else{
					Toast('您已签到');
				}
			},
			sign(){
				Toast('签到成功,已成功签到20天');
				this.isshow = false;
				this.count2 = true
			},
			goToCart(){
				this.$router.push({path:'/Cart'})
			},
			getMallList(){
				this.$http.get('./static/lessonList.json')
				.then(response=>{
					this.mallList = response.data.shopList;
					// console.log(response);
				})
			},
			goToSetting(){
				this.$router.push({path:'/Setting'})
			},
			toOther(info){
				// console.log(info);
				this.active = info;
				this.selected2 = info;
			},
			getDynamicList(){
				this.$http.get('./static/lessonList.json')
				.then(response=>{
					this.dynamicList = response.data.dynamicList;
					// console.log(this.dynamicList)
				})
			},
			// 点赞改变心和点赞数量
			favor(info){
				console.log(info)
				if (this.favored == false) {
					info.favor = info.favor+1;
					this.favored = true;
					info.bgImg = "../src/assets/home_btn_dz@2x2.png"
				}else{
					info.favor = info.favor-1;
					this.favored = false;
					info.bgImg = "../src/assets/home_btn_dz@2x1.png"
				}
				

			},
			toRecommend(){
				// console.log(this);
				this.active2 = '推荐';
				this.selected3 = '推荐';
				for (var i = 1; i < this.topicList.length; i++) {
					for (var j = 0; j < this.topicList.length-i; j++) {
						if (this.topicList[j].xx>this.topicList[j+1].xx) {
							var x = this.topicList[j];
							this.topicList[j] = this.topicList[j+1];
							this.topicList[j+1] = x;
							// console.log(this.topicList);
						}
					}
				}
			},
			toNew(){
				// console.log(this.topicList);
				this.active2 = '最新';
				this.selected3 = '最新';
				for (var i = 1; i < this.topicList.length; i++) {
					for (var j = 0; j < this.topicList.length-i; j++) {
						if (this.topicList[j].time<this.topicList[j+1].time) {
							var x = this.topicList[j];
							this.topicList[j] = this.topicList[j+1];
							this.topicList[j+1] = x;
							// console.log(this.topicList);
						}
					}
				}
			},
			toHot(){
				this.active2 = '最热';
				this.selected3 = '最热';
				for (var i = 1; i < this.topicList.length; i++) {
					for (var j = 0; j < this.topicList.length-i; j++) {
						if (this.topicList[j].hot<this.topicList[j+1].hot) {
							var x = this.topicList[j];
							this.topicList[j] = this.topicList[j+1];
							this.topicList[j+1] = x;
							// console.log(this.topicList);
						}
					}
				}
			},
			getTopicList(){
				this.$http.get('./static/lessonList.json')
				.then(response=>{
					this.topicList = response.data.topicList;
					// console.log(this.topicList)
				})
			},
			logIn(){
				this.$router.push({path:'./Login'})
			},
			getqaList(){
				this.$http.get('./static/lessonList.json')
				.then(response=>{
					this.qaList = response.data.qaList;
					// console.log(this.qaList);
				})
			},
			toVideo(){
				this.$router.push({path:'/Lesson'})
			},
			toBake(){
				this.$router.push({path:'/Bake'})
			},
			toVegetable(){
				this.$router.push({path:'/Vegetable'})
			},
			toTool(){
				this.$router.push({path:'/Tool'})
