<template>
	<div id="lesson">
		<div class="white_box"></div>
		<header class="clearfix">
			<div class="back" @click="$router.go(-1)"></div>
			<div class="title">在线美食课堂</div>
			<div class="cart"  @click="goToCart()"></div>
		</header>
		<section>
			<ul>
				<li v-for="item in menuList">
					<div class="img_box">
						<video :src="item.video" controls=""></video>
					</div>
					<div class="info_box clearfix">
						<div class="fl">
							<span>{{item.name}}</span>
							<div>
								<span class="score">{{item.score}}分</span>
								<span class="peopledid">{{item.peopledid}}人做过</span>
							</div>
						</div>
						<div class="fr">
							<img :src="item.headimg" alt=""><br>
							<div class="username">{{item.username}}</div>
						</div>
					</div>
				</li>
			</ul>
		</section>
	</div>
</template>
<script>
	export default{
		name:'lesson',
		data(){
			return{
				menuList:[]
			}
		},
		methods:{
			getMenuList(){
				this.$http.get('./static/menuList.json')
				.then(response=>{
					this.menuList = response.data.classList;
					// console.log(this.menuList);
				})
			},
			getmenuinfo(info){
				console.log(info);
				this.$store.dispatch('setMenuInfo',info);
				this.$router.push({path:'/MenuInfo'})
			},
			goToCart(){
				this.$router.push({path:'/Cart'})
			}
		},
		created(){
			this.getMenuList()
		}
	}
</script>
<style scoped>
	.white_box{
		height: 10px;
		position: fixed;
		width: 100%;
		top: 0;
		background-color: white;
		z-index: 100;
	}
	header{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		height: 50px;
		background-color: white;
		position: fixed;
		top: 10px;
		width: 98%;
		z-index: 100;
	}
	header>div{
		margin-top: 5px;
	}
	header .back{
		width: 40px;
		height: 40px;
		background-size: 40%;
		background-repeat: no-repeat;
		margin-top: 5px;
		background-image: url(../assets/nav_ht@2x.png);
	}
	header .title{
		font-size: 18px;
		height: 40px;
		color: #515151;
	}
	header .cart{
		width: 40px;
		height: 40px;
		background-size: 30px 30px;
		background-repeat: no-repeat;
		margin-top: 5px;
	}
	section{
		margin-top: 70px;
	}
	section ul{
		padding: 0;
	}
	section .img_box{
		width: 100%;
		margin-top: 25px;
	}
	section .img_box video{
		width: 100%;
	}
	section .info_box{
		margin-top: 10px;
	}
	section .info_box .fl>span{
		font-size: 24px;
		line-height: 70px;
		font-weight: bold;
		color: #515151;
	}
	section .info_box .fl div{
		font-size: 12px;
		color: #999999;
	}
	section .info_box .fl div .peopledid{
		margin-left: 15px;
	}
	section .fr img{
		width: 60px;
		height: 60px;
		border-radius: 50%;
	}
	section .fr div{
		font-size: 12px;
		color: #999999;
		text-align: center;
	}
</style>


