<template>
	<div id="changePwd">
	<div class="white_box"></div>
		<header class="clearfix">
			<div class="back" @click="$router.go(-1)"></div>
			<div class="title">修改密码</div>
			<div class="cart"></div>
		</header>
		<section>
			<div class="tip">为了您的账号安全，仅支持手机改密</div>
			<div class="name">{{username}}</div>
			<div >
				<div class="number_box box">
					<select>
						<option value="" v-for='item in countryNumList'>{{item.country}} + {{item.num}}</option>
					</select>
					<input type="text" placeholder="输入手机号" v-model="telNum">
				</div>
				<div class="code_tip box">
					<div class="code_div">短信验证码</div>
					<input type="text" class="code_box" v-model='code'>
					<input type="button" value="获取验证码" class="code_btn" @click='getCode(this)' v-if="show">
					<span v-if="!show" class="time">{{count}}s</span>
				</div>
				<div class="pwd_box box">
					<div class="pwd_div">设置密码</div>
					<input type="password" v-model="password">
				</div>
				<input type="button" value="确认提交" class="regist_btn" @click="sure()">
			</div>
		</section>
	</div>
</template>
<script>
	import { Toast } from 'mint-ui';
	export default{
		data(){
			return{
				username:"jiuge玖",
				password:'',
				code:'',
				countryNumList:[],
				show: true,
				timer: null,
				count: '',
				telNum:""

			}
		},
		methods:{
			getCountryNum(){
				this.$http.get("./static/countryNumList.json")
				.then(response=>{
					this.countryNumList = response.data.countryNum;
					// console.log(this.countryNumList)
				})
			},
			getCode(obj){
				if (!this.timer) {
					this.count = 60;
					this.show = false;
					this.timer = setInterval(() => {
						if (this.count > 0 && this.count <= 60) {
							this.count--;
						} else {
							this.show = true;
							clearInterval(this.timer);
							this.timer = null;
						}
					}, 1000)
				}
			},
			sure(){
				if (this.telNum == 0) {
					Toast('请输入手机号');
				}else {
					if (this.telNum.length < 11) {
						Toast('请输入正确的手机号');
					}else if (this.code == 0 || this.password == 0) {
						Toast('请填写完整');
					}else {
						this.$router.push({path:'./kit'})
					}
				}
			}
		},
		created(){
			this.getCountryNum()
		}
	}
</script>
<style scoped>
	.white_box{
		height: 10px;
		position: fixed;
		width: 100%;
		top: 0;
		background-color: white;
		z-index: 100;
	}
	header{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		height: 50px;
		background-color: white;
		position: fixed;
		top: 10px;
		width: 98%;
		z-index: 100;
	}
	header>div{
		margin-top: 5px;
	}
	header .back{
		width: 40px;
		height: 40px;
		background-size: 40%;
		background-repeat: no-repeat;
		margin-top: 5px;
		background-image: url(../assets/nav_ht@2x.png);
	}
	header .title{
		font-size: 18px;
		height: 40px;
		color: #515151;
	}
	header .cart{
		width: 40px;
		height: 40px;
	}
	.box{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
	}
	section{
		margin-top: 70px;
	}
	.tip{
		widows: 100%;
		height: 30px;
		line-height: 30px;
		font-size: 14px;
		color: #505050;
		text-align: center;
	}
	.name{
		margin-top: 50px;
		width: 100%;
		text-align: center;
		font-size: 18px;
		color: #505050;
	}

	.number_box{
		margin-top: 20px;
	}
	.number_box>select{
		width: 30%;
		height: 40px;
		border: none;
		color: #54a0ec;
		font-size: 15px;
		outline: none;
	}
	.number_box>input{
		width: 68%;
		height: 40px;
		border: none;
		font-size: 15px;
		outline: none;
	}
	.code_tip>div,.code_tip>input,.code_tip>span{
		height: 40px;
		line-height: 40px;
		font-size: 15px;
	}
	.code_div{
		width: 30%;
		color: #505050;
	}
	.code_box{
		width: 40%;
		outline: none;
		border: none;
		margin-left: 3%;
	}
	.code_btn{
		width: 30%;
		font-size: 13px;
		border-radius: 4px;
		border: none;
		outline: none;
		background-color: #fae268;
	}
	.time{
		text-align: center;
		background-color: #fae268;
		width: 30%;
		height: 40px;
		line-height: 40px;
		font-size: 13px;
		border-radius: 4px;
	}
	.name_box,.pwd_box{
		height: 40px;
		line-height: 40px;
		margin-top: 20px;
		color: #505050;
		font-size: 15px;
	}
	.name_box>input,.pwd_box>input{
		outline: none;
		border: none;
		width: 68%;
		font-size: 15px;
	}
	.tip{
		margin-top: 20px;
		font-size: 10px;
		color: #505050;
	}
	.tip>span{
		color: #83d1f2;
	}
	.regist_btn{
		margin-top: 20px;
		width: 100%;
		height: 40px;
		border: none;
		outline: none;
		background-color: #d8d8d8;
		color: white;
		border-radius: 4px;
	}
</style>


