<template>
	<div id="question">
		<div class="white_box"></div>
		<header>
			<div class="back" @click="$router.go(-1)"></div>
			<div class="title">问答</div>
			<div class="cart"></div>
		</header>
		<section>
			<div>扬州菜肴名扬天下，为全国四大菜系之一。扬州菜亦称维扬菜，是淮扬菜的一部分，迄今已有1600年的历史。扬州地入运河和长江的交汇地域，长期以来为南北交通的要冲，其腹地又是鱼米之乡，物产丰盛。这些优越的条件，使得扬州在历史上曾是繁荣一时的商业中心，当地的烹调技艺也因此得到了较好的发展，逐渐形成独具特色的风味菜——扬州菜。</div>
			<div>扬州著名的大菜有八宝葫芦、扒烧猪头、彩蝶飞舞、扬州五亭桥、琵琶对虾、菊花海螺等。红楼宴、三头宴、全藕宴是扬州菜肴的三颗明珠。
			红楼宴是《红楼梦》书中所写菜肴的创新。作者曹雪芹曾在扬州久留过，他的祖父曹寅到扬州接驾康熙皇帝时也曾设宴招待六司百宫。著名的红学家冯其庸说：“红楼菜实在是扬州菜的体系”。
			三头宴是将扬州的三道传统名菜：拆烩鲢鱼头、清炖狮子头、扒烧整猪头发展而成的宴席。狮子头肥嫩不腻；鲢鱼头口味香醇；整猪头香溢四座，均具有浓郁的乡土风味。
			全藕宴是历来被称为“荷藕之乡”的宝应根据祖先遗传的制作方法，又创新出50余种藕菜肴，形成的独特筵席。
			扬州菜肴除保持原淮扬特色的三头宴、富春包子、烧尾宴外，又创新推出琼花宴、河鲜宴、菊花蟹宴、野味宴、八怪宴、行宫宴、乾隆御宴、全素宴、火锅系列等一系列菜品，其特色菜大煮干丝、将军过桥、醋溜桂鱼、三套鸭等不可不尝。</div>
		</section>
