<template>
	<div id="info">
		<div class="white_box"></div>
		<header class="clearfix">
			<div class="back" @click="$router.go(-1)"></div>
			<div class="title">本周流行菜谱</div>
			<div class="cart"  @click="goToCart()"></div>
		</header>
		<section>
			<img :src="this.menuInfo.img" alt="">
			<div class="box">
				<div class="info">
					<div class="name">{{this.menuInfo.name}}</div>
					<span class="score">{{this.menuInfo.score}}分</span><span class="did">{{this.menuInfo.peopledid}}人做过</span>
				</div>
				<div class="user">
					<img :src="this.menuInfo.headimg" alt="" class="headimg">
					<div class="username">{{this.menuInfo.username}}</div>
				</div>
			</div>
			<ul>
				<li v-for="item in this.menuInfo.step">{{item.step}}</li>
			</ul>
		</section>
	</div>
</template>
<script>
	export default{
		name:"info",
		data(){
			return{
				menuinfo:''
			}
		},
		methods:{
			getmenuinfo(){
				// console.log(this.menuInfo);
				this.$http.get('./static/menuList.json')
				.then(response=>{
					this.menuinfo = response.data.menuList;
					// console.log(this.menuinfo)
				})
			}
		},
		created(){
			this.getmenuinfo()
		},
		computed:{
			menuInfo:function(){
				return this.$store.getters.getMenuInfo;
			}
		}
	}
</script>
<style scoped>
	.white_box{
		height: 10px;
		position: fixed;
		width: 100%;
		top: 0;
		background-color: white;
		z-index: 100;
	}
	header{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		height: 50px;
		background-color: white;
		position: fixed;
		top: 10px;
		width: 98%;
		z-index: 100;
	}
	header>div{
		margin-top: 5px;
	}
	header .back{
		width: 40px;
		height: 40px;
		background-size: 40%;
		background-repeat: no-repeat;
		margin-top: 5px;
		background-image: url(../assets/nav_ht@2x.png);
	}
	header .title{
		font-size: 18px;
		height: 40px;
		color: #515151;
	}
	header .cart{
		width: 40px;
		height: 40px;
		background-size: 30px 30px;
		background-repeat: no-repeat;
		margin-top: 5px;
		background-image: url(../assets/nav_gwc@2x.png);
	}
	.box{
		display: flex;
		justify-content: space-between;
	}
	section{
		margin-top: 70px;
	}
	section>img{
		width: 100%;
	}
	.name{
		font-size: 24px;
		line-height: 70px;
		font-weight: bold;
		color: #515151;
	}
	.headimg{
		margin-top: 5px;
		width: 60px;
		height: 60px;
		border-radius: 50%;
	}
	span{
		font-size: 12px;
		color: #999999;
	}
	.did{
		margin-left: 30px;
	}
	.username{
		font-size: 12px;
		color: #999999;
		text-align: center;
	}
	li{
		margin-top: 10px;
		font-size: 14px;
		color: #999999;
	}
</style>


