// store配置
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
const store = new Vuex.Store({
	state:{
		menuinfo:'',
		detail1:'',
		cart:[],
		userId:[],
		userPwd:[],
		login:false
	},
	mutations:{
		setMenuInfo(state,info){
			state.menuinfo = info;
		},
		setDetail1(state,info){
			state.detail1 = info;
		},
		setCart(state,info){
			if (state.cart.length == 0) {
				// console.log('空的');
				state.cart.push(info);
				// console.log(state.cart)
			}else {
				for (var i = 0; i < state.cart.length; i++) {
					if (state.cart[i].name == info.name) {
						// console.log('有了');
						state.cart[i].count++
						return
					}
				}
				// console.log('还没有');
				state.cart.push(info);
				// console.log(state.cart);
			}
			// console.log(state.cart);
			// state.cart.push(info);
			// console.log(state.cart);
		},
		setUserId(state,info){
			state.userId.push(info);
			// console.log(state.userId)
		},
		setUserPwd(state,info){
			state.userPwd.push(info);
			// console.log(state.userPwd)
		},
		setLogin(state,info){
			state.login = info;
			// console.log(state.login);
		}
	},
	actions:{
		setMenuInfo({commit},info){
			commit('setMenuInfo',info);
		},
		setDetail1({commit},info){
			commit('setDetail1',info);
		},
		setCart({commit},info){
			commit('setCart',info)
		},
		setUserId({commit},info){
			commit('setUserId',info)
		},
		setUserPwd({commit},info){
			commit('setUserPwd',info)
		},
		setLogin({commit},info){
			commit('setLogin',info)
		}
	},
	getters:{
		getMenuInfo(state){
			return state.menuinfo;
		},
		getDetail1(state){
			return state.detail1;
		},
		getCart(state){
			return state.cart;
		},
		getUserId(state){
			return state.userId;
		},
		getUserPwd(state){
			return state.userPwd;
		},
		getLogin(state){
			return state.login;
		}
	}
})
export default store;


