<template>
	<div id="goodthing">
		<div class="white_box"></div>
		<header class="clearfix">
	  		<div class="back" @click="$router.go(-1)"></div>
	  		<div class="title">厨房精选</div>
	  		<div class="cart" @click="goToCart()"></div>
		</header>
		<section>
			<div class="slider_box">
				<mt-swipe :auto="4000">
					<mt-swipe-item><img src="../../static/goodThingImg/gt_01.png" alt=""></mt-swipe-item>
					<mt-swipe-item><img src="../../static/goodThingImg/gt_01.png" alt=""></mt-swipe-item>
					<mt-swipe-item><img src="../../static/goodThingImg/gt_01.png" alt=""></mt-swipe-item>
				</mt-swipe>
			</div>
			<div class="nav_box box">
				<div @click="toTool()">
					<img src="../../static/goodThingImg/gt_06.png" alt=""><br>
					<span>精选小厨电</span>
				</div>
				<div @click="toWine()">
					<img src="../../static/goodThingImg/gt_07.png" alt=""><br>
					<span>高人气酒单</span>
				</div>
				<div @click="toBake()">
					<img src="../../static/goodThingImg/gt_08.png" alt=""><br>
					<span>烘焙发烧友</span>
				</div>
				<div @click="toTool()">
					<img src="../../static/goodThingImg/gt_09.png" alt=""><br>
					<span>厨房小工具</span>
				</div>
			</div>
			<div class="choose_box box">
				<div class="clearfix" @click="goToMenu()">
					<img src="../../static/goodThingImg/gt_02.png" alt="" @click="toMenu()">
					<div class="snack clearfix">
						<span class="first fl">销魂速食</span><br><div class="second clearfix"><span class="fl">在家吃到</span><br><span class="fl">各地的特色小吃</span></div>
					</div>
				</div>
				<div class="clearfix">
					<img src="../../static/goodThingImg/gt_03.png" alt="" @click="toVegetable()">
					<div class="new clearfix">
						<span class="first fl">新品专区</span><br><div class="second clearfix"><span class="fl">每周2,4上新</span><br><span class="fl">限时88折起</span></div>
					</div>
				</div>
			</div>
			<div class="shop_box">
				<div class="box list_box">
					<div @click = "toFirst()" :class="{active:selected == '默认'}">默认</div>
					<div @click = "toSale()" :class="{active:selected == '销量'}">销量</div>
					<div @click = "toPrice()" :class="{active:selected == '价格'}">价格</div>
					<div @click = "toClass()" :class="{active:selected == '分类'}">分类</div>
				</div>
				<div class="shop" v-if="selected=='默认'">
					<div v-for="item in shopinfo"  @click="todetail1(item)">
						<div class="img_box">
							<img :src="item.img" alt="">
						</div>
						<span class="des">{{item.des}}</span>
						<div>
							<span class="name">{{item.name}}</span><br>
							<div class="clearfix">
								<div class="fl">
									<span class="price">￥{{item.price}}</span><span class="mail">{{item.mail}}</span>
								</div>
								<span class="fr sale">已售{{item.sale}}</span>
							</div>
						</div>
					</div>
				</div>
				<div class="shop" v-if="selected=='销量'">
					<div v-for="item in shopinfo2" @click="todetail1(item)">
						<div class="img_box">
							<img :src="item.img" alt="">
						</div>
						<span class="des">{{item.des}}</span>
						<div>
							<span class="name">{{item.name}}</span><br>
							<div class="clearfix">
								<div class="fl">
									<span class="price">￥{{item.price}}</span><span class="mail">{{item.mail}}</span>
								</div>
								<span class="fr sale">已售{{item.sale}}</span>
							</div>
						</div>
					</div>
				</div>
				<div class="shop" v-if="selected=='价格'">
					<div v-for="item in shopinfo3"  @click="todetail1(item)">
						<div class="img_box">
							<img :src="item.img" alt="">
						</div>
						<span class="des">{{item.des}}</span>
						<div>
							<span class="name">{{item.name}}</span><br>
							<div class="clearfix">
								<div class="fl">
									<span class="price">￥{{item.price}}</span><span class="mail">{{item.mail}}</span>
								</div>
								<span class="fr sale">已售{{item.sale}}</span>
							</div>
						</div>
					</div>
				</div>
				<div class="shop" v-if="selected=='分类'">
					<div class="btn_box box">
						<div @click="toAll()" :class="{active:selected2 == '全部分类'}">全部分类</div>
						<div @click="toCookware()" :class="{active:selected2 == '锅具类'}">锅具类</div>
						<div @click="toTool1()" :class="{active:selected2 == '刀具类'}">刀具类</div>
					</div>
					<div v-if="selected2=='全部分类'" class="shop_box2">
						<div v-for="item in shopinfo" >
							<div class="img_box" @click="todetail1(item)">
								<img :src="item.img" alt="">
							</div>
							<span class="des">{{item.des}}</span>
							<div>
								<span class="name">{{item.name}}</span><br>
								<div class="clearfix">
									<div class="fl">
										<span class="price">￥{{item.price}}</span><span class="mail">{{item.mail}}</span>
									</div>
									<span class="fr sale">已售{{item.sale}}</span>
								</div>
							</div>
						</div>
					</div>
					<div v-if="selected2=='锅具类'" class="shop_box2">
						<div v-for="item in shopinfo" v-if="item.class==1">
							<div class="img_box" @click="todetail1(item)">
								<img :src="item.img" alt="">
							</div>
							<span class="des">{{item.des}}</span>
							<div>
								<span class="name">{{item.name}}</span><br>
								<div class="clearfix">
									<div class="fl">
										<span class="price">￥{{item.price}}</span><span class="mail">{{item.mail}}</span>
									</div>
									<span class="fr sale">已售{{item.sale}}</span>
								</div>
							</div>
						</div>
					</div>
					<div v-if="selected2=='刀具类'" class="shop_box2">
						<div v-for="item in shopinfo" v-if="item.class==2">
							<div class="img_box" @click="todetail1(item)">
								<img :src="item.img" alt="">
							</div>
							<span class="des">{{item.des}}</span>
							<div>
								<span class="name">{{item.name}}</span><br>
								<div class="clearfix">
									<div class="fl">
										<span class="price">￥{{item.price}}</span><span class="mail">{{item.mail}}</span>
									</div>
									<span class="fr sale">已售{{item.sale}}</span>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</section>
	</div>
</template>
<script>
	export default{
		name:"goodthing",
		data(){
			return{
				infoList:[],
				selected:0,
				shopinfo:[],
				shopinfo2:[],
				shopinfo3:[],
				active:'默认',
				selected:'默认',
				selected2:'全部分类'
			}
		},
		methods:{
			getinfolist(){
				this.$http.get('./static/shopInfo.json')
				.then(response=>{
					this.infoList = response.data.infoList;
					// console.log(this.infoList)
				})
			},
			getshopinfo(){
				this.$http.get('./static/shopInfo.json')
				.then(response=>{
					this.shopinfo = response.data.shopInfo;
					// console.log(this.shopinfo)
				})
			},
			getshopinfo2(){
				this.$http.get('./static/shopInfo.json')
				.then(response=>{
					this.shopinfo2 = response.data.shopInfo2;
					// console.log(this.shopinfo2)
				})
			},
			getshopinfo3(){
				this.$http.get('./static/shopInfo.json')
				.then(response=>{
					this.shopinfo3 = response.data.shopInfo3;
					// console.log(this.shopinfo3)
				})
			},
			toFirst(){
				this.active = '默认';
				this.selected = '默认';
			},
			toSale(){
				this.active = '销量';
				this.selected = '销量';
			},
			toPrice(){
				this.active = '价格';
				this.selected = '价格'
			},
			toClass(){
				this.active = '分类';
				this.selected = '分类'
			},
			goToCart(){
				this.$router.push({path:'/Cart'})
			},
			toAll(){
				this.selected2 = '全部分类'
			},
			toCookware(){
				this.selected2 = '锅具类'
			},
			toTool1(){

				this.selected2 = '刀具类';
			},
			toTool(){
				this.$router.push({path:'/Tool'})
			},
			toMenu(){
				this.$router.push({path:'/Menu'})
			},
			toBake(){
				this.$router.push({path:'/Bake'})
			},
			toWine(){
				this.$router.push({path:'/Wine'})
			},
			toVegetable(){
				this.$router.push({path:'/Vegetable'})
			},
			todetail1(info){
				this.$store.dispatch('setDetail1',info);
				this.$router.push({path:'/Detail'})
			},
			goToMenu(){
				this.$router.push({path:'/Menu'})
				// console.log(666)
			}
		},
		created(){
			this.getinfolist(),
			this.getshopinfo(),
			this.getshopinfo2(),
			this.getshopinfo3()
		}
	}
</script>
<style scoped>
	.box{
		display: flex;
		justify-content: space-between;
	}
	.white_box{
		height: 10px;
		position: fixed;
		width: 100%;
		top: 0;
		background-color: white;
		z-index: 100;
	}
	header{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		height: 50px;
		background-color: white;
		position: fixed;
		top: 10px;
		width: 98%;
		z-index: 100;
	}
	header>div{
		margin-top: 5px;
	}
	header .back{
		width: 40px;
		height: 40px;
		background-size: 40%;
		background-repeat: no-repeat;
		margin-top: 5px;
		background-image: url(../assets/nav_ht@2x.png);
	}
	header .title{
		font-size: 18px;
		height: 40px;
		color: #515151;
	}
	header .cart{
		width: 40px;
		height: 40px;
		background-size: 30px 30px;
		background-repeat: no-repeat;
		margin-top: 5px;
		background-image: url(../assets/nav_gwc@2x.png);
	}
	section .slider_box{
		height: 110px;
		width: 100%;
		margin-top: 70px;
	}
	section .slider_box img{
		width: 100%;
	}
	section .nav_box{
		margin-top: 10px;
	}
	section .nav_box div{
		text-align: center;
	}
	section .nav_box div img{
		width: 70%;
	}
	section .nav_box div span{
		font-size: 10px;
		color: #666666;
	}
	section .choose_box{
		margin-top: 20px;
	}
	section .choose_box div{
		text-align: center;
		position: relative;
	}
	section .choose_box div img{
		width: 95%;
	}
	section .choose_box>div>div{
		position: absolute;
		top: 15%;
		right: 5%;
	}
	section .choose_box>div>div .first{
		font-size: 16px;
	}
	section .choose_box>div>div .second{
		font-size: 9px;
		margin-top: 15%;
	}
	section .choose_box>div .snack{
		color: white;
	}
	section .shop_box>div.list_box{
		/*margin: 0;*/
		padding-left: 0;
		background-color: #ebebe2;
		height: 35px;
	}
	section .shop_box>div.list_box>div{
		width: 25%;
		line-height: 35px;
		font-size: 14px;
		text-align: center;
		position: relative;
	}
	.active{
		color: red;
	}
	.active:after{
		width: 80%;
		height: 2px;
		position: absolute;
		left: 10%;
		bottom: 0;
		content: '';
		background-color: red;
	}
	section .shop{
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
	}
	section .shop>div{
		width: 47%;
		margin-top: 10px;
	}
	section .shop>div>span{
		font-size: 10px;
		color: #222222;
	}
	section .shop .name{
		font-size: 9px;
		color: #999999;
	}
	section .shop .price{
		color: #fb0909;
	}
	section .shop .mail{
		margin-left: 10px;
		background-color: #fb0909;
		color: white;
		font-size: 7px;
	}
	section .shop .sale{
		font-size: 9px;
		color: #999999;
	}
	section .shop>div img{
		width: 100%;
	}
	section .shop>.btn_box{
		width: 100%;
		position: relative;
	}
	section .shop>.btn_box>div{
		width: 32%;
		text-align: center;
		border: none;
		outline: none;
		background-color: white;
		position: relative;
		height: 30px;
	}
	section .shop>div.shop_box2{
		width: 100%;
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
	}
	.shop_box2>div{
		width: 48%;
	}
	span.des{
		display: inline-block;
		height: 63px;
	}
</style>


