<template>
	<div id="wine">
		<div class="white_box"></div>
		<header class="clearfix">
			<div class="back" @click="$router.go(-1)"></div>
			<div class="title">高人气酒单</div>
			<div class="cart"  @click="goToCart()"></div>
		</header>
		<section>
			<div class="shop_box" v-for="item in wineList">
				<div class="box" @click="toDetail1(item)">
					<div class="img_box">
						<img :src="item.img" alt="">
					</div>
					<div class="info_box box2">
						<div class="name">{{item.name}}</div>
						<div class="price">价格：{{item.price}}</div>
					</div>
				</div>
				<div class="box3">
					<input type="button" value="加入购物车" class="btn1" @click="addCart(item)">
					<input type="button" value="立即购买" class="btn2" @click="buyNow(item.id)">
				</div>
				<div class="buy_box" v-if="isshow == true">
					<div class="box" v-for="item2 in wineList" v-if="item2.id == id">
						<div class="img_box">
							<img :src="item2.img" alt="">
						</div>
						<div class="info_box box2">
							<div class="name">{{item2.name}}</div>
							<div class="price">价格：{{item2.price}}</div>
						</div>
					</div>
					<input type="button" value="购买" class="buy" @click="buy()">
					<div class="close" @click="close()">×</div>
				</div>
			</div>
		</section>
	</div>
</template>
<script>
	import { Toast } from 'mint-ui';
	export default{
		name:"wine",
		data(){
			return{
				wineList:[],
				isshow:false,
				id:''
			}
		},
		methods:{
			getBakeList(){
				this.$http.get('./static/allList.json')
				.then(response=>{
					this.wineList = response.data.wineList
				})
			},
			addCart(info){
				// console.log(info);
				this.$store.dispatch('setCart',info)
				Toast('添加购物车成功');
			},
			goToCart(){
				this.$router.push({path:'/Cart'})
			},
			buyNow(info){
				this.isshow = true;
				// console.log(info);
				this.id = info;
			},
			close(){
				this.isshow = false;
			},
			buy(){
				this.isshow = false;
				Toast('购买成功');
			},
			toDetail1(info){
				this.$store.dispatch('setDetail1',info);
				this.$router.push({path:'/Detail'})
			}
		},
		created(){
			this.getBakeList()
		}
	}
</script>
<style scoped>
	.white_box{
		height: 10px;
		position: fixed;
		width: 100%;
		top: 0;
		background-color: white;
		z-index: 100;
	}
	header{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		height: 50px;
		background-color: white;
		position: fixed;
		top: 10px;
		width: 98%;
		z-index: 100;
	}
	header>div{
		margin-top: 5px;
	}
	header .back{
		width: 40px;
		height: 40px;
		background-size: 40%;
		background-repeat: no-repeat;
		margin-top: 5px;
		background-image: url(../assets/nav_ht@2x.png);
	}
	header .title{
		font-size: 18px;
		height: 40px;
		color: #515151;
	}
	header .cart{
		width: 40px;
		height: 40px;
		background-size: 30px 30px;
		background-repeat: no-repeat;
		margin-top: 5px;
		background-image: url(../assets/nav_gwc@2x.png);
	}
	.box,.box3{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		margin-top: 20px;
	}
	.box{
		height: 150px;
	}
	section{
		margin-top: 70px;
	}
	.shop_box{
		width: 100%;
		height: 200px;
	}
	.shop_box .img_box{
		width: 40%;
		height: 200px;
	}
	.shop_box .img_box>img{
		width: 100%;
		height: 150px;
	}
	.shop_box .info_box{
		width: 55%;
		height: 150px;
	}
	.box2{
		display: flex;
		flex-direction: column;
		justify-content: space-around;
	}
	.name{
		font-size: 14px;
		color: #666666;
	}
	.price{
		font-size: 10px;
		color: red;
	}
	input{
		margin-top: 5px;
		width: 48%;
		height: 25px;
		border: none;
		outline: none;
		border-radius: 4px;
	}
	.btn1{
		background-color: #ebc901;
	}
	.btn2{
		background-color: #ea2000;
	}
	.buy_box{
		height: 250px;
		position: fixed;
		z-index: 100;
		bottom: 0;
		background-color: rgba(255,255,255,0.9);
		width: 97%;
	}
	.close{
		position: absolute;
		right: 5px;
		top:0;
	}
	.buy{
		width: 100%;
		margin-top: 30px;
	}
</style>


